.contacts {
  padding-bottom: 113px;
  background-color: var(--section-background-coloured-color);
  @media screen and (min-width: 768px) {
    padding-bottom: 158px;
  }
  @media screen and (min-width: 1280px) {
    padding-bottom: 165px;
  }
}

.contacts__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 50px;
  padding-right: 50px;
  @media screen and (min-width: 768px) {
    padding-left: 36px;
    padding-right: 36px;
  }
  @media screen and (min-width: 1280px) {
    padding-left: 115px;
    padding-right: 115px;
    background-color: var(--section-background-coloured-color);
    background-image: url('../images/pc/contacts-bg.png');
    background-repeat: no-repeat;
    background-position: top;
    background-size: auto;
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url('../images/pc/contacts-bg@2x.png');
    }
  }
}

.contacts__list {
  margin-top: -84px;
  margin-bottom: 54px;
  @include flex();
  flex-direction: column;
  @media screen and (min-width: 768px) {
    flex-direction: row;
    margin-top: -78px;
    margin-bottom: 81px;
  }
  @media screen and (min-width: 1280px) {
    margin-bottom: 86px;
    margin-top: -102px;
  }
}
.contacts__item {
  background-color: #fff;
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
  border-radius: 24px;
}

.category {
  height: 427px;
  padding: (43px 20px 43px 21px);

  @media screen and (max-width: 767px) {
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
  @media screen and (min-width: 768px) {
    margin: 0;
    min-width: 220px;
    &:not(:last-child) {
      margin-right: 20px;
    }
    height: 544px;
    padding: (50px 31px 50px 32px);
  }
  @media screen and (min-width: 1280px) {
    width: 330px;
    height: 572px;
    padding: (51px 43px 62px 44px);
  }
}
.category__name {
  margin-bottom: 25px;
  width: 80px;
  height: 16px;
  background: var(--first-contact-background-color);
  color: var(--section-text-on-dark-bg-color);
  border-radius: 4px;
  @include flex();
  text-align: center;
  @include font(500, 12px, 1.83, 0.04em);
  @media screen and (min-width: 768px) {
    width: 73px;
    height: 28px;
    @include font(500, 16px, 1.88, 0.04em);
    border-radius: 6px;
    margin-bottom: 30px;
  }
  @media screen and (min-width: 1280px) {
    margin-bottom: 32px;
  }
}
.category__name-another {
  margin-bottom: 25px;
  width: 130px;
  height: 16px;
  background: var(--second-contact-background-color);
  color: var(--section-text-on-dark-bg-color);
  border-radius: 4px;
  @include flex();
  text-align: center;
  @include font(500, 12px, 1.83, 0.04em);
  @media screen and (min-width: 768px) {
    width: 138px;
    height: 28px;
    @include font(500, 16px, 1.88, 0.04em);
    border-radius: 6px;
    margin-bottom: 30px;
  }
  @media screen and (min-width: 1280px) {
    margin-bottom: 32px;
  }
}
.category__link {
  @include font(500, 12px, 1.5, 0.04em);
  color: var(--section-text-second-color);
  @media screen and (min-width: 768px) {
    @include font(500, 14px, 1.86, 0em);
  }
  @media screen and (min-width: 1280px) {
    @include font(500, 16px, 1.81, 0em);
  }
}

.about-category__item {
  display: flex;
  flex-direction: column;

  &:not(:last-child) {
    border-bottom: 1px solid #e1e1e1;
  }
  &:not(:first-child) {
    margin-top: 25px;
  }
  @media screen and (min-width: 768px) {
    &:not(:first-child) {
      margin-top: 30px;
    }
  }
  @media screen and (min-width: 1280px) {
    &:not(:first-child) {
      margin-top: 32px;
    }
  }
}
.about-category__name {
  @include font(500, 12px, 1.83, 0.04em);
  color: var(--section-text-main-color);
  @media screen and (min-width: 768px) {
    @include font(500, 14px, 1.86, 0.04em);
  }
  @media screen and (min-width: 1280px) {
    @include font(500, 16px, 1.88, 0.04em);
  }
}
.about-category__text {
  @include font(500, 12px, 1.5, 0em);
  color: var(--section-text-second-color);
  margin-top: 10px;
  margin-bottom: 25px;
  @media screen and (min-width: 768px) {
    @include font(500, 14px, 1.64, 0em);
    margin-bottom: 30px;
    margin-top: 14px;
  }
  @media screen and (min-width: 1280px) {
    @include font(500, 16px, 1.81, 0em);
    margin-top: 10px;
    margin-bottom: 32px;
  }
}

.schedule__weekdays {
  margin-bottom: 10px;
  @media screen and (min-width: 768px) {
    margin-bottom: 16px;
  }
  @media screen and (min-width: 1280px) {
    margin-bottom: 15px;
  }
}

.schedule__weekends {
  margin-bottom: 25px;
  @media screen and (min-width: 768px) {
    margin-bottom: 30px;
  }
  @media screen and (min-width: 1280px) {
    margin-bottom: 32px;
  }
}

.schedule__days {
  @include font(500, 12px, 1.83, 0em);
  color: var(--section-text-main-color);
  @media screen and (min-width: 768px) {
    @include font(500, 14px, 1.86, 0em);
  }
  @media screen and (min-width: 1280px) {
    @include font(500, 16px, 1.88, 0.04em);
  }
}

.schedule__time {
  @include font(500, 12px, 1.83, 0em);
  color: var(--section-label-text-color);
  @media screen and (min-width: 768px) {
    @include font(500, 14px, 1.86, 0em);
  }
  @media screen and (min-width: 1280px) {
    @include font(500, 16px, 1.88, 0.04em);
  }
}
